'use client';

import { useState, useEffect, useRef } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useNotification } from '@/context/NotificationContext';
import { useRouter } from 'next/navigation';
import { 
  FiSearch, 
  FiBell, 
  FiSettings, 
  FiCalendar, 
  FiChevronDown,
  FiPlus,
  FiFilter,
  FiFlag
} from 'react-icons/fi';

interface AdminHeaderProps {
  title: string;
  backButton?: boolean;
  onBack?: () => void;
  searchPlaceholder?: string;
  onSearch?: (query: string) => void;
  actionButton?: {
    label: string;
    onClick: () => void;
    icon?: React.ReactNode;
  };
  filterButton?: {
    onClick: () => void;
  };
}

const AdminHeader = ({
  title,
  backButton = false,
  onBack,
  searchPlaceholder = 'Search for anything here...',
  onSearch,
  actionButton,
  filterButton,
}: AdminHeaderProps) => {
  const { user, logout } = useAuth();
  const { showNotification } = useNotification();
  const router = useRouter();
  const [searchQuery, setSearchQuery] = useState('');
  const [showUserMenu, setShowUserMenu] = useState(false);
  const userMenuRef = useRef<HTMLDivElement>(null);

  const getInitials = (name: string) => {
    return name
      ?.split(' ')
      ?.map((n: string) => n[0])
      ?.join('')
      ?.toUpperCase()
      ?.slice(0, 2) || 'A';
  };

  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setSearchQuery(value);
    if (onSearch) {
      onSearch(value);
    }
  };

  // Close user menu when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (userMenuRef.current && !userMenuRef.current.contains(event.target as Node)) {
        setShowUserMenu(false);
      }
    };

    if (showUserMenu) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [showUserMenu]);

  const handleProfileSettings = () => {
    setShowUserMenu(false);
    // Navigate to profile page based on user role
    if (user?.role === 'admin') {
      // Admin should stay on dashboard - profile settings can be shown via modal or section
      // For now, just show a notification that admin profile management is available in dashboard
      showNotification('Admin profile settings are available in the dashboard', 'info');
      // Don't navigate away - admin should stay on current page
      return;
    } else if (user?.role === 'patient') {
      router.push('/profile');
    } else if (user?.role === 'doctor') {
      router.push('/doctor/profile');
    } else {
      router.push('/dashboard');
    }
  };

  const handleAccountSettings = () => {
    setShowUserMenu(false);
    // For admin, account settings can be managed in dashboard
    if (user?.role === 'admin') {
      showNotification('Account settings are available in the dashboard', 'info');
      // Don't navigate away
      return;
    }
    // For other roles, navigate to settings page if exists
    // router.push('/settings'); // Uncomment when settings page is created
    showNotification('Settings page coming soon', 'info');
  };

  const handleLogout = () => {
    setShowUserMenu(false);
    // Show confirmation using notification - user can proceed with logout
    if (window.confirm('Are you sure you want to logout?')) {
      showNotification('Logging out...', 'info', 1000);
      setTimeout(() => {
        logout();
      }, 500);
    }
  };

  return (
    <header className="bg-white border-b border-gray-200 px-8 py-4 sticky top-0 z-20">
      <div className="flex items-center justify-between">
        {/* Left Section - Title and Back Button */}
        <div className="flex items-center gap-4">
          {backButton && (
            <button
              onClick={onBack}
              className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
            >
              <svg className="w-5 h-5 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
            </button>
          )}
          <h1 className="text-2xl font-bold text-gray-800">{title}</h1>
        </div>

        {/* Center Section - Search */}
        <div className="flex-1 max-w-md mx-8">
          <div className="relative">
            <FiSearch className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
            <input
              type="text"
              placeholder={searchPlaceholder}
              value={searchQuery}
              onChange={handleSearch}
              className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            />
          </div>
        </div>

        {/* Right Section - Actions and User */}
        <div className="flex items-center gap-4">
          {actionButton && (
            <button
              onClick={actionButton.onClick}
              className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors text-sm font-medium"
            >
              {actionButton.icon || <FiPlus className="text-lg" />}
              {actionButton.label}
            </button>
          )}
          
          {filterButton && (
            <button
              onClick={filterButton.onClick}
              className="flex items-center gap-2 px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors text-sm font-medium text-gray-700"
            >
              <FiFilter className="text-lg" />
              Filters
            </button>
          )}

          <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
            <FiPlus className="text-xl text-gray-600" />
          </button>

          <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
            <FiBell className="text-xl text-gray-600" />
            <span className="absolute top-1 right-1 w-2 h-2 bg-red-500 rounded-full"></span>
          </button>

          <button className="p-2 hover:bg-gray-100 rounded-lg transition-colors">
            <FiSettings className="text-xl text-gray-600" />
          </button>

          <button className="p-2 hover:bg-gray-100 rounded-lg transition-colors">
            <FiCalendar className="text-xl text-gray-600" />
          </button>

          <div className="p-2 hover:bg-gray-100 rounded-lg transition-colors flex items-center gap-1">
            <FiFlag className="text-xl text-gray-600" />
            <span className="text-sm text-gray-600">1/4</span>
          </div>

          {/* User Profile Dropdown */}
          <div className="relative" ref={userMenuRef}>
            <button
              onClick={() => setShowUserMenu(!showUserMenu)}
              className="flex items-center gap-3 px-3 py-2 hover:bg-gray-100 rounded-lg transition-colors"
            >
              <div className="w-8 h-8 rounded-full bg-gradient-to-br from-teal-400 to-indigo-500 flex items-center justify-center text-white text-xs font-bold">
                {getInitials(user?.name || 'Admin')}
              </div>
              <div className="text-left hidden md:block">
                <p className="text-sm font-medium text-gray-900">{user?.name || 'Admin User'}</p>
                <p className="text-xs text-gray-500">Super admin</p>
              </div>
              <FiChevronDown className={`text-gray-600 hidden md:block transition-transform ${showUserMenu ? 'rotate-180' : ''}`} />
            </button>

            {showUserMenu && (
              <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border border-gray-200 py-2 z-50">
                <button
                  onClick={handleProfileSettings}
                  className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 transition-colors"
                >
                  Profile Settings
                </button>
                <button
                  onClick={handleAccountSettings}
                  className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-50 transition-colors"
                >
                  Account Settings
                </button>
                <div className="border-t border-gray-200 my-2"></div>
                <button
                  onClick={handleLogout}
                  className="w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50 transition-colors"
                >
                  Logout
                </button>
              </div>
            )}
          </div>
        </div>
      </div>
    </header>
  );
};

export default AdminHeader;
